import {
    Card,
    CardContent,
    Grid,
    CardActionArea,
    Avatar,
    Tooltip,
    styled
} from '@mui/material';
import AddTwoToneIcon from '@mui/icons-material/AddTwoTone';
import FormDialogEdit from './FormDialogEdit';
import { useState } from 'react';

const AvatarAddWrapper = styled(Avatar)(
    ({ theme }) => `
        background: ${theme.colors.alpha.black[5]};
        color: ${theme.colors.primary.main};
        width: ${theme.spacing(8)};
        height: ${theme.spacing(8)};
`
);

const CardAddAction = styled(Card)(
    ({ theme }) => `
        border: ${theme.colors.primary.main} dashed 1px;
        height: 100%;
        color: ${theme.colors.primary.main};
        box-shadow: none;
            
        .MuiCardActionArea-root {
            height: 100%;
            justify-content: center;
            align-items: center;
            display: flex;
        }
            
        .MuiTouchRipple-root {
            opacity: .2;
        }
            
        &:hover {
            border-color: ${theme.colors.alpha.black[100]};
        }
    `
);

export default function CardItemAdd(props) {
    const [open, setOpenDialog] = useState(false);

    const handleClickOpenDialog = () => {
        setOpenDialog(true);
    };

    const onClose = () => {
        setOpenDialog(false);
    };

    return (
        <Grid item xs={12} sm={3}>
            <Tooltip arrow title={props.data?.title || "Adicionar"}>
                <CardAddAction onClick={handleClickOpenDialog}>
                    <CardActionArea sx={{ px: 1 }}>
                        <CardContent>
                            <AvatarAddWrapper>
                                <AddTwoToneIcon fontSize="large" />
                            </AvatarAddWrapper>
                        </CardContent>
                    </CardActionArea>
                </CardAddAction>
            </Tooltip>

            <FormDialogEdit
                open={open}
                title={`${props.data?.section} / ${props.data?.title}`}
                jsonInfo={props.jsonInfo}
                onSave={props.onSave}
                onClose={onClose}
            />
            
        </Grid>
    );
}