import {
    Card,
    Grid,
    Typography,
    Radio,
    FormControlLabel,
    IconButton,
    Tooltip,
    Box,
    lighten,
    styled,
    Divider
  } from '@mui/material';
  import DeleteTwoToneIcon from '@mui/icons-material/DeleteTwoTone';
  import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
  import FormDialogEdit from './FormDialogEdit';
import FormDialogDelete from './FormDialogDelete';
  import { useState } from 'react';
  
  const CardLogo = styled('img')(
    ({ theme }) => `
        border: 1px solid ${theme.colors.alpha.black[30]};
        border-radius: ${theme.general.borderRadius};
        padding: ${theme.spacing(1)};
        margin-right: ${theme.spacing(2)};
        background: ${theme.colors.alpha.white[100]};
    `
  );
  
  const IconButtonEdit = styled(IconButton)(
    ({ theme }) => `
        background: ${theme.colors.secondary.lighter};
        color: ${theme.colors.secondary.main};
        padding: ${theme.spacing(0.5)};
  
        &:hover {
        background: ${lighten(theme.colors.secondary.lighter, 0.4)};
        }
    `
  );
  
  const IconButtonError = styled(IconButton)(
    ({ theme }) => `
        background: ${theme.colors.error.lighter};
        color: ${theme.colors.error.main};
        padding: ${theme.spacing(0.5)};
  
        &:hover {
        background: ${lighten(theme.colors.error.lighter, 0.4)};
        }
    `
  );
  
  const CardCc = styled(Card)(
    ({ theme }) => `
        border: 1px solid ${theme.colors.alpha.black[30]};
        background: ${theme.colors.alpha.black[5]};
        box-shadow: none;
    `
  );
  
  export default function CardItem(props) {
  
    const [open, setOpen] = useState(false);
    const [openDelete, setOpenDelete] = useState(false);

    const handleClickOpen = () => {
      setOpen(true);
    };
    const onClose = () => {
      setOpen(false);
    };

    const handleClickOpenDelete = () => {
      setOpenDelete(true);
    };
    const onCloseDelete = () => {
      setOpenDelete(false);
    };
  
    return (
        <>
            {/* ------------ VISUALIZAÇÃO ------------------- */}
            <Grid item xs={12} sm={3}>
                <CardCc sx={{ px: 2, pt: 2, pb: 1 }}>
                <Box display="flex" alignItems="center">
                    {/* <CardLogo
                        src="./static/images/placeholders/logo/mastercard.png"
                        alt="Visa"
                    /> */}
                    <Box>
                        <Typography variant="h3" fontWeight="normal">
                            {props.data.title}
                        </Typography>
                        <Typography variant="subtitle2">
                            {props.data.subtitle}
                        </Typography>
                        <Divider/>
                        { props.data.labels.map(function (label, idx) {
                            return (
                                <Typography key={idx} pt={1} color="text.secondary" variant="body2">
                                    {label}
                                </Typography> 
                            )
                        })}
                    </Box>
                </Box>
                <Box
                    pt={2}
                    display="flex"
                    alignItems="center"
                    justifyContent="end"
                >
                    <Tooltip arrow title="Editar">
                        <IconButtonEdit sx={{ ml: 1 }} onClick={handleClickOpen}>
                            <EditTwoToneIcon fontSize="small" />
                        </IconButtonEdit>
                    </Tooltip>
                    <Tooltip arrow title="Deletar">
                        <IconButtonError sx={{ ml: 1 }} onClick={handleClickOpenDelete}>
                            <DeleteTwoToneIcon fontSize="small" />
                        </IconButtonError>
                    </Tooltip>
                </Box>
                </CardCc>
            </Grid>
  
            {/* ------------ EDIÇÃO ------------------- */}
            <FormDialogEdit
                open={open}
                title={`${props.data.section} / ${props.data.title}`}
                jsonInfo={props.jsonInfo}
                onSave={props.onSave}
                onClose={onClose}
            />

            {/* ------------ EXCLUSÃO ------------------- */}
            <FormDialogDelete
                open={openDelete}
                title={`${props.data.section} / ${props.data.title}`}
                jsonInfo={props.jsonInfo}
                onSave={props.onSave}
                onClose={onCloseDelete}
            />
        </>
    );
  }