import { Helmet } from 'react-helmet-async';
import { useNavigate, useParams } from 'react-router-dom';
import { Link as RouterLink } from 'react-router-dom';
import { Tooltip, Grid, Container, Typography, Button } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import PageTitleWrapper from 'src/components/PageTitleWrapper';
import Footer from 'src/components/Footer';
import { useGetInventoryByIdQuery, useUpdateInventoryMutation } from 'src/services/baseApi';
import Content from './Content';

function InventoryHome() {
  let { id } = useParams();
  const navigate = useNavigate();
  const isNewInventory = !id;
  const inventoryId = id ? parseInt(id) : 0;

  const { data, isLoading, isSuccess } = !isNewInventory ? useGetInventoryByIdQuery(
    { inventoryId },
    { refetchOnMountOrArgChange: true, refetchOnFocus: true, refetchOnReconnect: true }
  ) : { data: {
    id: 0,
    id_usuario: 0,
    data_criacao: new Date().toISOString(),
    data_atualizacao: new Date().toISOString(),
    data_remocao: null,
    calculos: {},
    cliente: {
      nome: '',
      endereco: {
        rua: "",
        cidade: "",
        estado: "",
        pais: ""
      },
    },
    decujus: {
      nome: '',
      endereco: {
        rua: "",
        cidade: "",
        estado: "",
        pais: ""
      },
    },
    familiares: [],
    imoveis: [],
    veiculos: [],
    investimentos: [],
    participacoes_societarias: [],
    creditos: [],
    criptomoedas: [],
    outros_bens: [],
    seguros: [],
    bens_internacionais: [],
    dividas: []
  }, isLoading: false, isSuccess: true };

  const [updateInventory] = useUpdateInventoryMutation();

  const handleSave = async (updatedData, isArray, itemId, isItemToDelete) => {
    try {
      await updateInventory({
        inventoryId,
        data: updatedData,
        isArray,
        itemId,
        isItemToDelete,
      });
    } catch (error) {
      console.error('Failed to save inventory:', error);
    }
  };

  if (!isNewInventory) {
    if (isLoading) return (<h1>Carregando</h1>)
    if (!isSuccess) {
      navigate('/inventories/list');
      return null;
    }
  }

  return (
    <>
      <Helmet>
        <title>Detalhes do Inventário</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              {isNewInventory ? 'Novo Inventário' : `Inventário de ${data.decujus.nome}`}
            </Typography>
            <Typography variant="subtitle2">
              Visualize aqui informações detalhadas do inventário
            </Typography>
          </Grid>
          <Grid item>
            <Tooltip arrow title="Voltar para lista de inventários">
              <Button
                sx={{ mt: { xs: 2, md: 0 } }}
                variant="outlined"
                startIcon={<ArrowBackIcon fontSize="small" />}
                component={RouterLink}
                to="/inventories/list"
              >
                Voltar
              </Button>
            </Tooltip>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            <Content
              data={data}
              onSave={handleSave}
            />
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  );
}

export default InventoryHome;
